import React, { useCallback, useState, useContext } from 'react';


export const ToolbarContext = React.createContext<
{ mode: 'singleLine' | 'doubleLine', toolbarWidth: number | null, setToolbarWidth: (w: number) => void }
>({ mode: 'doubleLine', toolbarWidth: null, setToolbarWidth: () => {} });

export const ToolbarContextProvider: React.FC<{ mode: 'singleLine' | 'doubleLine'}> = (props) => {
  const [toolbarWidth, setToolbarWidth_] = useState<number | null>(null);
  const setToolbarWidth = useCallback((n: number) => {
    setToolbarWidth_(n);
  }, []);
  return (
    <ToolbarContext.Provider value={{ mode: props.mode, toolbarWidth, setToolbarWidth }}>
      {props.children}
    </ToolbarContext.Provider>
  );
}

export const DoubleLineRowContext = React.createContext<boolean>(false);
